import axios from 'axios';

export const api = {
  login: (data) => axios.post('http://127.0.0.1:8000/api/v1/user/auth', data),
  createUser: (data) => axios.post('http://127.0.0.1:8000/api/v1/user/create', data),
  profileUpdate: (data) => {
    console.log(data)
    axios.post('http://127.0.0.1:8000/api/v1/user/profile/update', data)
  },
}
